
image{
  width: 100%;
}
.landing{
  position: relative;
  background: url("./image/showcase.jpg");
  background-size: cover;
  background-position: center;
  height: 100vh;
  margin-top: -24px;
  margin-bottom: -50px;
}
.landing-inner{
  padding-top: 80px;
}
.dark-overlay{
  background-color: rgb(0, 0, 0, 0.7);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.card-form{
  opacity: 0.9;
}
.latest-profile-img{
  width: 40px;
  height: 40px;
}
.form-control::-webkit-input-placeholder{
  color: #bbb !important;
}
.form-control:-ms-input-placeholder{
  color: #bbb !important;
}
.form-control::-ms-input-placeholder{
  color: #bbb !important;
}
.form-control::placeholder{
  color: #bbb !important;
}


